<template>
    <div class="newsDetail">
      <div class="banner">
          <div class="container">
            <img src="../assets/img/newsBg.jpg" alt="">
          </div>
      </div>
      <div class="detail">
          <div class="container">
              <bread :data="[{name:'首页',url:'/'},{name:'职位招聘',url:''},{name:'专业不限'.name,url:''}]"></bread>
              <div class="content">
                <p class="title">发布时间 ：2020年11月11日 来源 ：刘姝雯 编辑 ：廖芹 浏览量 ：5955</p>
                <div class="context">
      <p>摘要：本文通过政策推动和市场导向的视角，分析了垃圾焚烧市场状况，结合四种常见焚烧炉的工作原理、特点及市场需求，探索环保技术应用方向，旨在为国家的污染防治工作提供切实可行的技术方案。在以后的垃圾焚烧的过程中，机械炉排炉将继续作为生活垃圾焚烧炉首选炉型，回转窑焚烧炉在危险废物处理的过程发挥重要重要。</p>



      <p>关键词：垃圾焚烧炉；生活垃圾；危险废弃物</p>



      <p>随着国民经济及城市建设的发展、环保标准的提高，新建垃圾填埋场受到越来越多的限制，已经很难找到合适的场址。自20世纪80年代后期，国内城市生活垃圾处理行业开始关注现代垃圾焚烧技术。1985年开工、1988年投产的深圳市市政环卫综合处理厂是我国第一座垃圾焚烧处理电站。在2000年之前垃圾焚烧厂数量仅仅为2；2009年，全国在运行的垃圾焚烧厂数量只有74座，截至2019年2月底,我国已建成生活垃圾焚烧发电项目超过400座。垃圾焚烧工程在国内迅速扩展，在中国已成燎原之势，已被确定为我国生活垃圾处理的主流工艺。垃圾焚烧发电的核心设备是焚烧炉，选定垃圾焚烧炉后再与之配套相应的辅助工艺。因此，本文通过分析垃圾焚烧市场状况，结合常见焚烧炉的工作原理及特点，探讨未来的垃圾焚烧技术走向，为环境保护提供一些参考思路。</p>
      



      <p>2020年新一轮周期开启之后，国内的垃圾焚烧项目将会陆续推出，机械炉排炉将继续作为生活垃圾焚烧炉首选炉型，回转窑焚烧炉在危险废物处理的过程发挥重要重要，流化床焚烧炉不会规模使用，CAO焚烧炉基本不被使用。根据当前形势研判，预计“十四五”期间，垃圾焚烧将由工程时代进入运营时代，企业应当根据工艺需要提前引导做好运营人才的培养工作。</p>



<p>参考文献</p>



<p>[1] 聂永丰.三废处理工程技术手册（固体废物卷）.北京：化学工业出版社.2000.</p>



<p>[2]黄蔚雯,蔡培,俞坤嵛.机械式炉排垃圾焚烧炉的应用[J].热力发电,2009,38(06):49-52.</p>



<p>[3]方朝军,任超峰,王武忠,王汝佩,王玉生,覃军建.浅析大容量生活垃圾循环流化床焚烧炉的技术特点与调试运行[J].工业锅炉,2019(03):37-40.</p>



<p>[4]吴浩,王艳宜,吴燕琦,李倬舸.深圳市生活垃圾分类对垃圾焚烧影响的研究[J].环境卫生工程,2016,24(01):40-43.</p>



<p>[5]裴照堂,吴伟祥.回转窑焚烧炉在医疗废物处理中的应用[J].环境工程,2007(02):55-57+4.</p>



<p>[6]赵绪新,王恩堂,马晓茜.CAO垃圾焚烧系统热力模型研究[J].工业加热,2001(01):14-16.</p>
